import Toast from '@/wxcomponents/vant-weapp/dist/toast/toast';
export default {
	data() {
		return {
			isSing: true,
			showSelectWord: false,
			showSelectBook: false,
			showClearBox: false,
			columns: ['10', '20', '30', '40', '50', '60', '70'],
			wordsNum: 0,
			thesaurusArr: [],
			dailyPracticeList: []
		}
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		//选择每日学习词数弹出框 打开弹出层
		showPopupWord() {
			this.showSelectWord = true
		},
		// 选择每日学习词数弹出框 关闭弹出层
		onCloseWord() {
			this.showSelectWord = false
		},
		//选择每日学习词数弹出框   picker选择器取消事件
		onCancelWord() {
			// Toast('取消');
			this.showSelectWord = false
		},
		// 修改计划单词数 选择每日学习词数弹出框   picker选择器确认事件  
		onConfirmWord(event) {
			const {
				picker,
				value,
				index
			} = event.detail;
			// Toast(`当前值：${value}, 当前索引：${index}`);
			// this.wordsNum = value
			this.$minApi.modifyPlanWords({
				backwordPlanId: this.dailyPracticeList.backwordPlanId,
				planCount: parseInt(value)
			}).then(data => {
				console.log(data)
				Toast("修改成功！")
				this.dailyPractice()
			})
			this.showSelectWord = false
		},
		// 选择图书已学完弹框   打开弹出层
		showPopupBook() {
			this.showSelectBook = true
		},
		// 选择图书已学完弹框   关闭弹出层
		onCloseBook() {
			this.showSelectBook = false
		},
		// 清除学习记录弹框   打开弹出层
		showPopupClear() {
			this.showClearBox = true
		},
		// 清除学习记录弹框   关闭弹出层
		onCloseClear() {
			this.showClearBox = false
		},
		// 获取所有词书列表
		thesaurusList() {
			this.$minApi.thesaurusList({}).then(data => {
				console.log(data)
				this.thesaurusArr = data.data
			})
		},
		// 获取当前图书
		dailyPractice() {
			this.$minApi.dailyPractice({}).then(data => {
				this.dailyPracticeList = data.data
				this.wordsNum = data.data.planCount
				console.log(data)
			})
		},
		// 清除学习记录
		clearThesaurus() {
			this.$minApi.clearThesaurus({
				backwordPlanId: this.dailyPracticeList.backwordPlanId
			}).then(data => {
				console.log(data)
				this.onCloseClear()
				Toast('清除记录成功！')
				this.thesaurusList()
				this.dailyPractice()
			})
		},
		// 修改学习词库
		modifyPlanThesaurus(backwordPlanId) {
			this.$minApi.modifyPlanThesaurus({
				backwordPlanId: backwordPlanId
			}).then(data => {
				console.log(data)
				this.dailyPractice()
				this.thesaurusList()
				Toast("修改成功！")
			})
		},

	},
	created() {
		this.thesaurusList(),
			this.dailyPractice()
	}
}
